import { AuthService } from './auth.service';
import { environment } from './../../../environments/environment';
import { UserService } from './user.service';
import { IConversation } from './../../../interfaces/chat/conversation';
import { Injectable } from '@angular/core';
import { Subject } from 'rxjs/Subject';
import * as io from 'socket.io-client';
import { IUser } from '../../../interfaces';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Injectable()
export class SocketService {

    public socket: BehaviorSubject<SocketIOClient.Socket> = new BehaviorSubject<SocketIOClient.Socket>(null);
    public thing;

    constructor(private authService: AuthService) {
        this.authService.authToken.subscribe(token => {
            if (this.socket.getValue() !== null) {
                this.socket.getValue().close();
                this.socket.next(null);
            }
            if (token) {
                const sock = io(environment.api.url, { query: `token=${token}`, transports: ['polling', 'websocket'] });
                console.log(sock.id); // undefined
                sock.on('connect', (socket) => {
                    alert('cosa');
                });
                sock.on('connect_error', () => {
                    this.thing = true;
                });
                sock.on('connect_timeout', () => {
                    this.thing = true;
                });
                sock.on('reconnect_attempt', () => {
                    this.thing = true;
                });
                sock.on('reconnecting', () => {
                    this.thing = true;
                });
                sock.on('reconnect_error', () => {
                    this.thing = true;
                });
                sock.on('reconnect_failed', () => {
                    this.thing = true;
                });
                sock.on('ping', () => {
                    this.thing = true;
                });
                sock.on('pong', () => {
                    this.thing = true;
                });
                this.socket.next(sock);
            }
        });
    }
}



